.bckOrder{
    background: url("https://res.cloudinary.com/ds7jaqjll/image/upload/v1664835144/Wish-Him/congratsAnimation_qydj3z.gif") no-repeat;
    background-size:100% 90%;
    overflow:visible;
    position: absolute;
    width: 90%;
    margin-top: 6rem;
    margin-left: 5%;
    margin-right: 5%;
    text-align: center;
    height: 85%;
    font-family: 'Roboto Mono', monospace;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
}

.titleSuccess{
    font-size:3.25rem;
    font-weight:bold;
}

.textSuccess{
    font-size: x-large;
}

.btnOrder{
    display: inline-block;
    border-radius: 0.25rem;
    background-color: #358483;
    border: none;
    text-align: center;
    font-size: 1rem;
    transition: all 0.5s;
    cursor: pointer;
    margin: 0.31rem;
    color: aliceblue;
    font-family: 'Roboto Mono', monospace;
    height: 3.125rem;
    width: 7.2rem;
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.btnOrder:hover {
    padding-right: 1rem;
    opacity: 1;
    right: 0;
}